import { Component, OnInit } from '@angular/core';
import { MenubarModule, MenuItem } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
//订单管理
@Component({
  selector: 'app-order-manage',
  templateUrl: './order-manage.component.html',
  styleUrls: ['./order-manage.component.scss'],
  providers: [Commfunc]
})
export class OrderManageComponent implements OnInit {
  items1: MenuItem[];
  items2: MenuItem[];
  items3: MenuItem[];
  show: number;
  constructor(public commfunc: Commfunc) { }

  ngOnInit() {
    this.items1 = [
      {
        label: '订单查询',
        routerLink: '/pages/tzb/order/order-center/order-manage/order-query'
      }
    ];
    this.items2 = [
      {
        label: '订单异常处理',
        routerLink: '/pages/tzb/order/order-center/order-manage/order-exception-handle'
      }
    ];
    // this.items3 = [
    //   {
    //     label: '订单异常处理',
    //     routerLink: '/pages/tzb/order/order-center/order-manage/order-exception-handle'
    //   }
    // ]
    this.judgStatu();
  }
  judgStatu() {
    let url = window.location.href;
    let urlSplit = url.split('/');
    urlSplit.map(v => {
      if (v === 'order-query') {
        this.show = 1;
      } else if (v === 'order-exception-handle') {
        this.show = 2;
      }
      // else if (v === 'voucher-manage') {
      //   this.show = 3;
      // }else if (v === 'voucher-applicate') {
      //   this.show = 4;
      // }else if (v === 'voucher-applicate') {
      //   this.show = 5;
      // }
    })
  }
  apply(num) {
    this.show = num;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
