import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { OrderManageComponent } from './order-manage.component';
import { OrderExceptionHandleComponent } from './order-exception-handle/order-exception-handle.component';
import { OrderQueryComponent } from './order-query/order-query.component';
// import { VoucherManageComponent } from './voucher-manage/voucher-manage.component';
// import { VoucherApplicateComponent } from './voucher-applicate/voucher-applicate.component';
// import { CustomVoucherManageComponent } from './custom-voucher-manage/custom-voucher-manage.component'

const routes: Routes = [
    {
        path: '',
        component: OrderManageComponent,
        children: [
            { path: '', redirectTo: 'order-query', pathMatch: 'full' },
            {
                path: 'order-query', component: OrderQueryComponent,
            },
            {
                path: 'order-exception-handle', component: OrderExceptionHandleComponent,
            },
            // {
            //     path: 'voucher-manage', component: VoucherManageComponent,
            // },
            // {
            //     path: 'voucher-applicate', component: VoucherApplicateComponent,
            // },
            // {
            //     path: 'custom-voucher-manage', component: CustomVoucherManageComponent,
            // },
        ]
    },
];

export const OrderManageRouting = RouterModule.forChild(routes);