import { Component, OnInit } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderExceptionHandleBean } from './bean/order-exception-handle.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { OrderHttpService } from './../../../http/order.http.service';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
// import { OrderStatus } from '../../../constant/codeValue'
//订单异常处理
@Component({
  selector: 'app-order-exception-handle',
  templateUrl: './order-exception-handle.component.html',
  styleUrls: ['./order-exception-handle.component.scss'],
  providers: [Commfunc, ConfirmationService]
})
export class OrderExceptionHandleComponent implements OnInit {

  //表数据
  tableData: any = [];
  //订单状态
  orderStatus: any = [];
  //详情模态框
  exceptionDisplay: boolean = false;
  //bean 
  orderExceptionHandleBean: OrderExceptionHandleBean = new OrderExceptionHandleBean();
  //分页
  total: string; //总页数
  first: number = 0;
  //提示信息
  msgs: Message[] = [];
  //交易日期
  ch: any = CALENDAR_CN;//英文转化为中文
  dealDateFrom: Date = new Date();
  dealDateTo: Date = new Date();
  //校验
  userform: FormGroup;
  //点击重置按钮，验证信息隐藏
  flag: any = true;
  code: any;

  constructor(
    public fb: FormBuilder,
    public orderHttpService: OrderHttpService,
    public commfunc: Commfunc,
  ) {
    this.orderExceptionHandleBean.pageParam.pageSize = 10;
    this.orderExceptionHandleBean.pageParam.pageNumber = 1;
    this.codeValues() //调用方法，获取全部码值
    this.orderStatus = this.code['OrderStatus'];

  }

  ngOnInit() {
    //订单状态初始化
    this.orderExceptionHandleBean.orderHeader.orderStatus = 'ORDER_APPROVED';
    //表单校验
    this.userform = this.fb.group({
      'orderNo': new FormControl('', Validators.compose([Validators.maxLength(16), specialSymbol])),
      'appBranchNo': new FormControl(''),
      'appUserId': new FormControl(''),
      'orderStatus': new FormControl(''),
      'coreSeqNo': new FormControl(''),
      'dealDateFrom': new FormControl(''),
      'dealDateTo': new FormControl(''),
    });


    //订单状态下拉框
    // this.orderHttpService.queryOrderStatus({}).subscribe(data => {
    //   if (data.returnCode.code == 'success') {
    //     this.orderStatus = this.commfunc.toChangeTree(data.orderStatusList, "description", "orderStatus");
    //     this.orderStatus.unshift({ label: "请选择", value: '' });
    //     console.log(this.orderStatus)
    //   } else {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
    //   }
    // }, error => {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    // })
    // this.query();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //查询点击事件
  queryClick() {
    this.orderExceptionHandleBean.pageParam.pageNumber = 1;
    this.first = 0;
    this.query();

  }

  //查询方法
  query() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //点击查询，验证不通过出现提示信息
    this.flag = true;
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    //查询时间
    this.orderExceptionHandleBean.orderHeader.queryStartDate = this.commfunc.transDateN(this.dealDateFrom);
    this.orderExceptionHandleBean.orderHeader.queryEndDate = this.commfunc.transDateN(this.dealDateTo);
    //去除字符串左右空格
    for (var items in this.orderExceptionHandleBean) {
      for (var item in this.orderExceptionHandleBean[items]) {
        this.orderExceptionHandleBean[items][item] = this.trim(this.orderExceptionHandleBean[items][item]);
      }
    }

    this.orderHttpService.queryOrderList(this.orderExceptionHandleBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.orderList;
        this.total = data.pageParam.totalCount + '';
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //异常处理按钮
  exceptionClick(item) {
    let param = {
      orderNo: item.orderNo, //订单编号
      coreSeqNo: item.coreSeqNo //流水号
    }
    this.orderHttpService.compensateOrderExt(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '异常处理提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '异常处理提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '异常处理提示', detail: '调用服务失败！' });
    })
  }


  //分页
  paginate(event) {
    //每页显示的条数
    this.orderExceptionHandleBean.pageParam.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.orderExceptionHandleBean.pageParam.pageNumber = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.query();
  }

  //重置
  resetClick() {
    this.orderExceptionHandleBean = new OrderExceptionHandleBean();
    this.dealDateFrom = null;//清空开始日期
    this.dealDateTo = null;//清空结束日期
    this.orderExceptionHandleBean.pageParam.pageSize = 10;
    this.orderExceptionHandleBean.pageParam.pageNumber = 1;
    //点击重置按钮，信息提示消失
    this.flag = false;
  }

  //去除字符串的左右空格方法
  trim(str) {
    if (typeof str == "string") {
      return str.replace(/^\s*|\s*$/, "");
    } else {
      return str;
    }
  }

  //日期范围的处理
  selectTime1() {
    if (this.dealDateFrom && this.dealDateTo) {
      if (this.dealDateFrom > this.dealDateTo) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.dealDateFrom = null;
        })
      }
    }
  }
  selectTime2() {
    if (this.dealDateFrom && this.dealDateTo) {
      if (this.dealDateFrom > this.dealDateTo) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.dealDateTo = null;
        })
      }
    }
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
