import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderHttpService } from './../../../../http/order.http.service';
import { Message } from 'primeng/primeng';
import { OrderQueryBean } from './../bean/order-query.bean';
// 修改
@Component({
    selector: 'order-query-detail',
    styleUrls: ['order-query-detail.component.scss'],
    templateUrl: 'order-query-detail.component.html'
})

export class OrderQueryDetail implements OnChanges {
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //校验
    userform: FormGroup;
    //提示信息
    msgs: Message[] = [];
    //实体
    orderQueryBean: OrderQueryBean = new OrderQueryBean();
    //表数据
    tableData: any = [];
    //接受调接口后返回的回显数据
    list: any = [];

    constructor(
        public fb: FormBuilder,
        public orderHttpService: OrderHttpService
    ) { }

    ngOnInit() {

    }

    ngOnChanges() {
        let param = {
            orderNo: this.inValue['orderNo'],//订单编号
            coreSeqNo: this.inValue['coreSeqNo'],//流水号
            clientNo: this.inValue['clientNo']//客户号
        };
        //详情接口
        this.orderHttpService.queryOrderDetail(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.orderDetailList[0]) {
                    this.list = data.orderDetailList[0];
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '订单详情回显提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '订单详情回显提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '订单详情回显提示', detail: "调用服务失败" });
        });
    }

    //确定按钮进行关闭
    addCloseBtn() {
        this.outValue.emit(false);
    }
}