import { Component, OnInit } from '@angular/core';
import { OrderQueryBean } from './bean/order-query.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { OrderHttpService } from './../../../http/order.http.service';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
// import { bankNo, salesChannelEnumId, tranType } from '../../../constant/codeValue';

//订单查询（订单概要查询）
@Component({
  selector: 'app-order-query',
  templateUrl: './order-query.component.html',
  styleUrls: ['./order-query.component.scss'],
  providers: [ConfirmationService, Commfunc]
})

export class OrderQueryComponent implements OnInit {
  //表数据
  tableData: any = [];
  //下拉框
  orderStatus: any = [];//订单状态
  payStatus: any = [];//支付状态
  fulStatus: any = [];//交付状态
  bankNo: any = [];//银行标识
  salesChannelEnumId: any = [];//来源渠道
  tranType: any = [];//交易类型
  // bean
  orderQueryBean: OrderQueryBean = new OrderQueryBean();
  //模态框
  headerTitle: string = "详情"; //模态框的标题
  display: boolean = false;  //模态框是否显示
  showModel: string;
  //修改的参数
  updateValue: any = [];
  //分页
  total: string; //总页数
  first: number = 0;
  //提示信息
  msgs: Message[] = [];
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  queryStartDate: Date = new Date();

  queryEndDate: Date = new Date();
  //校验
  userform: FormGroup;
  code: any;


  constructor(
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.orderQueryBean.pageParam.pageSize = 10;
    this.orderQueryBean.pageParam.pageNumber = 1;
    this.codeValues() //调用方法，获取全部码值
    this.bankNo = this.code['bankNo'];
    this.salesChannelEnumId = this.code['salesChannelEnumId'];
    this.tranType = this.code['tranType']

  }

  ngOnInit() {
    this.preSet();
    //调查询
    // this.query();

    //订单状态下拉框
    this.orderHttpService.queryOrderStatus({}).subscribe(data => {
      if (data.returnCode.code == 'success') {

        this.orderStatus = this.commfunc.toChangeTree(data.orderStatusList, "description", "orderStatus");
        this.orderStatus.unshift({ label: "请选择", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
    //支付状态查询
    this.orderHttpService.queryOrderPayStatus({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.payStatus = this.commfunc.toChangeTree(data.orderPayStatusList, "description", "orderStatus");
        this.payStatus.unshift({ label: "请选择", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
    //交付状态查询
    this.orderHttpService.queryOrderFulStatus({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.fulStatus = this.commfunc.toChangeTree(data.orderFulStatusList, "description", "orderStatus");
        this.fulStatus.unshift({ label: "请选择", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  preSet() {
    //表单校验
    this.userform = this.fb.group({
      'orderNo': new FormControl('', Validators.compose([Validators.maxLength(20), specialSymbol])),
      'queryStartDate': new FormControl('', Validators.required),
      'queryEndDate': new FormControl('', Validators.required),
      'appBranchNo': new FormControl('', Validators.compose([specialSymbol])),
      'paySeqNo': new FormControl('', Validators.compose([specialSymbol])),
      'fulSeqNo': new FormControl('', Validators.compose([specialSymbol])),
      'appUserId': new FormControl('', Validators.compose([specialSymbol])),
      'productCode': new FormControl('', Validators.compose([specialSymbol])),
      'settleAcctNo': new FormControl('', Validators.compose([specialSymbol])),
      'minAmt': new FormControl('', Validators.compose([specialSymbol])),
      'maxAmt': new FormControl('', Validators.compose([specialSymbol]))
    });
  }
  //查询点击事件
  queryClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //交易金额范围处理
    if (this.orderQueryBean.orderHeader.minAmt && this.orderQueryBean.orderHeader.maxAmt) {
      if (this.orderQueryBean.orderHeader.minAmt > this.orderQueryBean.orderHeader.maxAmt) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '最小交易金额不能大于最大交易金额' });
        setTimeout(() => {
          this.orderQueryBean.orderHeader.maxAmt = '';
        });
        return
      }
    }
    this.orderQueryBean.pageParam.pageNumber = 1;
    this.first = 0;
    this.query();
  }


  //查询
  query() {
    //查询时间
    this.orderQueryBean.orderHeader.queryStartDate = this.commfunc.transDateN(this.queryStartDate);
    this.orderQueryBean.orderHeader.queryEndDate = this.commfunc.transDateN(this.queryEndDate);

    this.orderHttpService.queryOrderList(this.orderQueryBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.orderList;
        this.total = data.pageParam.totalCount + '';
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //详情按钮事件
  detailClick(item) {
    this.headerTitle = '订单详情';
    this.display = true;
    this.showModel = '1';
    this.updateValue = item;
  }

  //详情的回调
  detailCall(param) {
    this.display = param;
    this.query();
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.orderQueryBean.pageParam.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.orderQueryBean.pageParam.pageNumber = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.query();
  }

  //重置
  resetClick() {
    this.orderQueryBean = new OrderQueryBean();
    this.queryStartDate = new Date();
    this.queryEndDate = new Date();
    this.orderQueryBean.pageParam.pageSize = 10;
    this.orderQueryBean.pageParam.pageNumber = 1;
  }
  //日期范围的处理
  selectTime1() {
    this.selectTime(1);
  }
  selectTime2() {
    this.queryStartDate.getHours() == new Date().getHours() ? this.selectTime(2) : this.selectTime(1);
  }
  selectTime(a) {
    if (this.queryStartDate && this.queryEndDate) {
      if (this.queryStartDate > this.queryEndDate) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.queryStartDate = null;
          this.queryEndDate = null;
        })
      }
      let startTime = this.queryStartDate.getTime();
      let endTime = this.queryEndDate.getTime()
      let time = Math.floor((endTime - startTime) / 1000 / 3600 / 24) + a;
      if (time > 7) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期和结束日期间隔不能大于七天' });
        setTimeout(() => {
          this.queryStartDate = null;
          this.queryEndDate = null;
        })
      }
    }
  }




}